html {
  font-family: Arial, sans-serif;
}

body {
  justify-content: center;
  margin: 0;
  display: flex;
}

.people {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 40px 30px;
  width: 100%;
  padding-top: 30px;
  display: grid;
}

@media screen and (width <= 999px) {
  .people {
    grid-template-columns: repeat(2, 200px);
    overflow: hidden;
  }
}

.person {
  flex-direction: column;
  align-items: center;
  width: 220px;
  height: 287px;
  display: flex;
  position: relative;
}

.photo {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
}

.details {
  text-align: center;
}

.picture {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: all 1s;
}

.rank {
  box-sizing: border-box;
  color: #8b0000;
  text-align: center;
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
  display: flex;
  position: absolute;
  top: 95px;
  right: 20px;
}

.name {
  color: #2f4f4f;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  line-height: 23px;
  transition: all 1s;
}

.extra {
  visibility: hidden;
  margin-top: 9px;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 500;
  line-height: 20.7px;
}

.person:hover {
  background-color: #eee;
  border-radius: 10px;
  transform: scale(1.2);
}

.person:hover .picture {
  border-radius: 60px;
}

.person:hover .extra {
  visibility: visible;
}

.sport:after {
  content: ",";
}

.pay:before {
  content: "$";
}

.pay:after {
  content: "M";
}
